.icon-jijianxinxi {
  color: #43cb83;
}

.icon-shou {
  color: #0f88eb;
}

#sender .tab {
  width: 100%;
  height: 1.066667rem;
  background: #f3f3f3;
  font-size: 0.373333rem;
  color: #4c4c4c;
  text-align: center;
}

#sender .sendAdr li {
  font-size: 0.373333rem;
  padding: 0.533333rem 0;
}

#sender .sendAdr li div:nth-of-type(1) {
  font-size: 0.666667rem;
}

#sender .sendAdr li div:nth-of-type(2) {
  padding-left: 0.133333rem;
  color: #9f9da8;
}

#sender .sendAdr li div:nth-of-type(3) {
  color: #0f88eb;
  padding-left: 0.266667rem;
}

#sender .sendAdr {
  padding: 0 0.4rem;
  background: #fff;
}

#sender .sendInfo {
  background: #fff;
  margin-top: 0.266667rem;
}

#sender .sendInfo li {
  padding: 0.4rem 0.266667rem 0.4rem 0.4rem;
  font-size: 0.373333rem;
}

#sender .sendInfo li p:nth-of-type(1) {
  color: #4c4c4c;
}

#sender .sendInfo li p:nth-of-type(2) {
  color: #9f9da8;
  text-align: right;
}

#sender .sendInfo li p:nth-of-type(2) span {
  font-size: 0.373333rem;
}

#sender .remark {
  font-size: 0.373333rem;
  color: #4c4c4c;
  background: #fff;
  padding: 0.4rem;
}

#sender .remark textarea {
  display: block;
  border: 0;
  resize: none;
  width: 100%;
  color: inherit;
  line-height: inherit;
  outline: 0;
  padding-left: 0.4rem;
  box-sizing: border-box;
  height: 1.333333rem;
}

#sender .protocol {
  margin-bottom: 4rem;
  font-size: 0.32rem;
  margin-top: 0.266667rem;
  margin-left: 0.4rem;
  color: #9f9da8;
}

#sender .lastBtn .icon-wenhao-yuankuang {
  font-size: 0.373333rem;
  color: #9f9da8;
}

#sender .protocol .protocolInfo {
  color: #0f88eb;
}

#sender .protocol .check {
  display: inline-block;
  width: 0.4rem;
  height: 0.4rem;
  background-image: url('../assets/check_normal.png');
  background-repeat: no-repeat;
  background-size: 0.4rem;
}

#sender .protocol .check.on {
  background-image: url('../assets/Checked_on.png');
}

.lastBtn {
  box-sizing: border-box;
  font-size: 0.373333rem;
  padding: 0.266667rem 0.4rem;
  background: #fff;
  position: fixed;
  bottom: 1.2rem;
  width: 100%;
}

.lastBtn .priceTip {
  margin-bottom: 0.266667rem;
}

.lastBtn .btn {
  padding: 0.266667rem 0;
  border-radius: 0.053333rem;
  text-align: center;
  background: #0f88eb;
  color: #fff;
}

.lastBtn .price {
  color: #f34a17;
}

.lastBtn .price span:nth-of-type(1) {
  font-size: 0.933333rem;
}


